import React, { useRef, useEffect, useState } from "react";
import "./menu.css";
import "./bg.css"

const getDimensions = (ele) => {
  const { height } = ele.getBoundingClientRect();
  const offsetTop = ele.offsetTop;
  const offsetBottom = offsetTop + height;

  return {
    height,
    offsetTop,
    offsetBottom
  };
};

const scrollTo = (ele) => {
	console.log('clicked')
  ele.scrollIntoView({
    behavior: "smooth",
    block: "start"
  });
};

function Menu() {
  const [visibleSection, setVisibleSection] = useState();

  const headerRef = useRef(null);
  const leadershipRef = useRef(null);
  const providerRef = useRef(null);
  const operationsRef = useRef(null);

  const sectionRefs = [
    { section: "Leadership", ref: leadershipRef },
    { section: "Providers", ref: providerRef },
    { section: "Operations", ref: operationsRef }
  ];

  useEffect(() => {
    const handleScroll = () => {
      const { height: headerHeight } = getDimensions(headerRef.current);
      const scrollPosition = window.scrollY + headerHeight;

      const selected = sectionRefs.find(({ section, ref }) => {
        const ele = ref.current;
        if (ele) {
          const { offsetBottom, offsetTop } = getDimensions(ele);
          return scrollPosition > offsetTop && scrollPosition < offsetBottom;
        }
      });

      if (selected && selected.section !== visibleSection) {
        setVisibleSection(selected.section);
      } else if (!selected && visibleSection) {
        setVisibleSection(undefined);
      }
    };

    handleScroll();
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [visibleSection]);
  return (
		<div>
				<div className="bg-img">
					<div className='titleholder'>
								Kyle Hall
					</div>
				</div>
	      <div className="content">
					<div className="top-spacer"> </div>
				        	<div className="sticky">
							          	<div className="header" ref={headerRef}>
										            	<p>Hiello it me</p>
											            <button type="button" className={`header_link ${visibleSection === "Leadership" ? "selected" : ""}`} onClick={() => {scrollTo(leadershipRef.current);}}>
										              	Dingus
										            	</button>
																	<button type="button" className={`header_link ${visibleSection === "Providers" ? "selected" : ""}`} onClick={() => {scrollTo(providerRef.current);}}>
										              	Pringus
											            </button>
																	<button type="button" className={`header_link ${visibleSection === "Operations" ? "selected" : ""}`} onClick={() => {scrollTo(operationsRef.current);}}>
											              Onk
											            </button>
							          	</div>
				        	</div>
					        <div className="section" id="Leadership" ref={leadershipRef}/>
					        <div className="section" id="Providers" ref={providerRef} />
					        <div className="section" id="Operations" ref={operationsRef} />
					<div className="bottom-spacer"></div>
			</div>
		</div>

  );
}

export default Menu;
